.page_static {
	display: flex;
	flex-wrap: wrap;
	margin-bottom: rem(42);

	@include min768 {
		margin-bottom: rem(20);
	}

	img {
		width: 33%;
		margin-right: rem(34);

		@include min {
			width: auto;
			margin-right: 0;
			margin-bottom: rem(44);
		}

		@include min768 {
			width: 100%;
			margin-bottom: rem(20);
		}
	}

	.descr {
		flex: 1 0 0;
		margin: 0 !important;
		padding-right: rem(126);

		@include min {
			padding-right: 0;
		}
	}
}

.quote_text {
	margin: rem(50) 0;
	padding: rem(24) rem(112);
	background-color: #F0F7FF;
	border-radius: rem(10);
	text-align: center;

	@include min {
		margin: rem(32) 0;
	}

	@include min768 {
		padding: rem(20) rem(7);
	}

	.text {
		font-weight: 600;

		@include min {
			font-size: rem(16);
		}
	}
}

.tabs_wrap {
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	width: 100%;
	margin-bottom: rem(57);

	@include min768 {
		margin-bottom: rem(25);
	}

	.button-b-icon {
		padding: rem(18) rem(57);
		width: rem(250);

		@include min540 {
			width: 100%;
			text-align: center;
		}
		
		span {
			font-size: rem(20);
			text-transform: uppercase;
			font-weight: 600;
			color: #1C3E1F;

			@include min540 {
				width: 100%;
				text-align: center;
			}
		}

		&:first-child {
			border-top-right-radius: 0;
			border-bottom-right-radius: 0;

			@include min540 {
				border-top-right-radius: rem(49);
				border-bottom-left-radius: 0;
			}

			&::after {
				border-top-right-radius: 0;
				border-bottom-right-radius: 0;

				@include min540 {
					border-top-left-radius: rem(49);
					border-top-right-radius: rem(49);
					border-bottom-left-radius: 0;
					border-bottom-right-radius: 0;
				}
			}
		}

		&:last-child {
			border-top-left-radius: 0;
			border-bottom-left-radius: 0;
			background-image: linear-gradient(91.33deg, #05E176 8.71%, #05E192 88.93%);

			@include min540 {
				border-top-right-radius: 0;
				border-bottom-left-radius: rem(49);
				border-bottom-right-radius: rem(49);
			}

			&::after {
				border-top-left-radius: 0;
				border-bottom-left-radius: 0;
				// background-image: linear-gradient(91.33deg, #05E176 8.71%, #05E192 88.93%);
				@include min540 {
					border-top-right-radius: 0;
					border-bottom-left-radius: rem(49);
					border-bottom-right-radius: rem(49);
				}
			}

			// background-image: linear-gradient(91.33deg, #05E176 8.71%, #05E192 88.93%);
		}
	}

	.button-b-icon.active {
		&::after {
			background-color: transparent;
		}
	}
}

.colors {
	display: grid;
	grid-template-rows: auto;

	grid-template-columns: repeat(7, 1fr);

	@include min {
		grid-template-columns: repeat(5, 1fr);
	}

	@include min540 {
		grid-template-columns: repeat(3, 1fr);
	}
	
	.color_item {
		width: rem(157);
		// margin-right: rem(20);
		margin-bottom: rem(26);

		@include min {
			width: rem(130);
		}

		@include min540 {
			width: rem(100);
		}

		&-top {
			width: 100%;
			height: rem(178);
			border-radius: rem(10);
			overflow: hidden;
			margin-bottom: rem(14);
			border: rem(1) solid #AEBBCB;

			@include min {
				height: rem(115);
			}

			@include min540 {
				height: rem(90);
			}

			img {
				width: 100%;
				height: 100%;
				object-fit: cover;
			}
		}

		&-bottom {
			ul {
				li {
					font-size: rem(16);
					color: #373737;

					@include min {
						font-size: rem(14);
					}
				}
			}
		}
	}
}

.colors.smart {
	grid-template-columns: repeat(9, 1fr);

	@include min {
		grid-template-columns: repeat(6, 1fr);
	}

	@include min768 {
		grid-template-columns: repeat(4, 1fr);
	}

	.color_item {
		width: rem(120);

		@include min {
			width: calc(100% - 0.625rem);
		}

		&-top {
			height: rem(85);

			@include min540 {
				// max-height: rem(84);
				height: rem(68);
			}
		}
	}
}